.showcase video {
  position: absolute;
  top: 0;
  left: 0;
  width: 71%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  z-index: -1;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*background-color: rgba(13, 50, 77, 0.5);*/
  /*mix-blend-mode: overlay;*/
}

h2.heading {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 500;
  padding: 10px;
  color: whitesmoke;
  text-shadow: 0 0 2px black;
}

header {
  background-: #5075cf;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: whitesmoke;
  /*text-transform: uppercase;*/
}

form {
  width: 410px;
  margin: 50px auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  padding: 20px;
  position: absolute;
  margin-top: 10rem;
  right: 0;
  margin-right: 2rem;
  background-color: rgba(45, 52, 54, 0.9);
  z-index: 1000;
}

input {
  display: block;
  width: 100%;
  border-radius: 8px;
  border: 1px solid whitesmoke;
  height: 3rem;
  padding: 7px;
  background: #2d3436;
  color: inherit;
  font-family: inherit;
}

input:focus {
  outline: none !important;
  border-color: #719ece;
  box-shadow: 0 0 1px #719ece;
  padding: 7px;
}

.inputfield {
  padding: 7px;
}

input::placeholder {
  color: whitesmoke;
  padding: 7px;
}

.input-container {
  margin-bottom: 20px;
}

